import { gsap, selector } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { ScrollSmoother } from "gsap/ScrollSmoother";

gsap.registerPlugin(ScrollTrigger, ScrollSmoother);

class Post {
	constructor() {
		ScrollTrigger.defaults({
			scroller: "#app",
			markers: false,
		});
		this.smoother = ScrollSmoother.create({
			wrapper: "#app",
			content: "#wrapper",
			effects: true,
			smooth: 1,
			smoothTouch: 0.1,
		});
		this.opacityTrigger(".studio-baang-composition__title h2");
		this.opacityTrigger(".studio-baang-composition__description p");
		this.galleryAnimation(".studio-baang-gallery", ".wp-block-image");
		this.galleryAnimation(".studio-baang-cover__image--logo");
	}
	opacityTrigger(selector) {
		let animDom = gsap.utils.toArray(selector);
		animDom.forEach((e) => {
			gsap.defaults({
				ease: "none",
			});
			const anim = gsap.fromTo(
				e,
				{
					opacity: 0,
					transformOrigin: "0% 100%",
				},
				{
					opacity: 1,
					paused: true,
				}
			);
			ScrollTrigger.create({
				trigger: e,
				start: "center 80%",
				onEnter: () => anim.play(),
			});
			ScrollTrigger.create({
				trigger: e,
				start: "top bottom",
				onLeaveBack: () => anim.pause(0),
			});
		});
	}

	galleryAnimation(target, child) {
		const targetNodes = document.querySelectorAll(target);
		if (child != null) {
			targetNodes.forEach((targetNode) => {
				const childNodes = targetNode.querySelectorAll(child);
				this.scrollAnimation(childNodes);
			});
			return;
		}
		targetNodes.forEach((parentNode) => {
			this.scrollAnimation(parentNode);
		});
	}

	scrollAnimation(selector) {
		if (typeof selector == "object") {
			const arraySelector = gsap.utils.toArray(selector);
			const LIMITSPEED = 5;
			arraySelector.forEach((e, i) => {
				gsap.set(e, {
					zIndex: i + 1,
				});
			});
			this.smoother.effects(arraySelector, {
				speed: (index) => {
					return 0.5 + (index % LIMITSPEED) * 0.075;
				},
			});
			return;
		}
	}

	beforeLeave() {
		this.smoother.kill();
		ScrollTrigger.refresh();
	}
}

export { Post };
