import barba from "@barba/core";
import { debounce } from "lodash";
import { addBrowserClass } from "./modules/browser";

import { Post } from "./modules/post";

class App {
	constructor() {
		this.vh = 0;
		addBrowserClass();

		this.setBarba();

		this.onResize();
		window.addEventListener(
			"resize",
			debounce(this.onResize.bind(this)),
			false
		);
	}

	onResize(e) {
		this.vh = window.innerHeight;
		document.body.style.cssText = `--vh: ${this.vh / 100}px`;
	}

	setBarba() {
		let post;
		barba.init({
			debug: true,
			views: [
				{
					namespace: "home",
					afterEnter() {
						console.log("i'm in home!");
					},
				},
				{
					namespace: "post",
					afterEnter() {
						post = new Post();
					},
					beforeLeave() {
						post.beforeLeave();
						post = null;
					},
				},
			],
		});
		barba.hooks.enter(() => {
			window.scrollTo(0, 0);
		});
	}
}

window.onload = () => {
	new App();
};
